<div class="main"><PageHeader title="上传组件"></PageHeader><div class="show flex flex-row justify-between"><div class="content box-shadow-ccc base"><a-form><a-form-item label="上传图片"><upload-img v-bind="uploadOption" :oldImgSrc="`${uploadOption.hasImg?&quot;http://dummyimage.com/64x64&quot;:&quot;&quot;}`"><template #prefix v-if="uploadOption.icon"><a-icon :class="`${uploadOption.icon === &quot;icontuichu11&quot; ? &quot;icont&quot; : &quot;icon&quot;}`" :type="uploadOption.icon"></a-icon></template></upload-img></a-form-item></a-form></div><div class="card box-shadow-ccc base"><a-form><a-form-item label="组件禁用"><a-switch v-model:checked="uploadOption.disabled"></a-switch></a-form-item><a-form-item label="图片填充"><a-switch v-model:checked="uploadOption.hasImg"></a-switch></a-form-item><a-form-item label="限制大小"><a-input-number v-model:value="uploadOption.fileSize" :min="0"></a-input-number></a-form-item><a-form-item label="更换尺寸"><a-select style="width: 120px" v-model:value="uploadOption.aspectRatio" placeholder="请选择" :default-active-first-option="false" :filterOption="false"><a-select-option v-for="v in sizeList" :key="v.title" :value="v.aspectRatio">{{v.title}}</a-select-option></a-select></a-form-item><a-form-item label="切换icon"><a-select style="width: 120px" v-model:value="uploadOption.icon" placeholder="请选择" :default-active-first-option="false" :filterOption="false"><a-select-option v-for="v in iconList" :key="v.value" :value="v.value">{{v.title}}</a-select-option></a-select></a-form-item></a-form></div></div></div>