
import {
  Form, Select, InputNumber, Switch,
} from 'ant-design-vue';
import {
  defineAsyncComponent, defineComponent, reactive, ref,
} from 'vue';

const iconList = [
  {
    title: '上传',
    value: 'icontuichu11',
  },
  {
    title: '重置',
    value: 'iconzhongzhi',
  },
  {
    title: '分享',
    value: 'iconfenxiang',
  },
  {
    title: '查看',
    value: 'iconchakan',
  },
  {
    title: '下载',
    value: 'iconxiazai1',
  },
  // {
  //   title: '编辑',
  //   value: 'iconbianji4',
  // },
  // {
  //   title: '筛选',
  //   value: 'iconshaixuan1',
  // },
  // {
  //   title: '排序',
  //   value: 'iconorder-asc',
  // },
];
const aspectRatioList = [
  {
    aspectRatio: 2,
    title: '2:1',
  },
  {
    aspectRatio: 16 / 9,
    title: '16:9',
  },
  {
    aspectRatio: 4 / 3,
    title: '4:3',
  },
  {
    aspectRatio: 1,
    title: '1:1',
  },
];

export default defineComponent({
  components: {
    PageHeader: defineAsyncComponent(() => import('@/components/Public/Exhibition/PageHeader.vue')),
    UploadImg: defineAsyncComponent(() => import('@/components/UploadImg.vue')),
    aForm: Form,
    aFormItem: Form.Item,
    aSelect: Select,
    aSelectOption: Select.Option,
    aInputNumber: InputNumber,
    aSwitch: Switch,
    aIcon: defineAsyncComponent(() => import('@/components/BaseIcon.vue')),
  },
  props: {},
  setup() {
    const uploadOption = reactive({
      fileSize: 2,
      hasImg: true,
      aspectRatio: 1,
      disabled: false,
      icon: 'icontuichu11',
    });
    const sizeList = ref(aspectRatioList);

    return {
      uploadOption,
      iconList,
      sizeList,
    };
  },
});
